import './index.scss'
import { Link, useHistory } from 'react-router-dom'
import { Tooltip, Button } from 'antd';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import axios from 'axios';
import erc1155 from 'Assets/erc1155.png'
import erc721 from 'Assets/erc721.png'
import { getFloat } from 'Utils/helper';
import { getSymbol } from 'Utils/getContractAddress';
export default function ProductionItem(props) {
    const { tokenURI, isERC1155, collectionAddress, index, price, tokenPayment, creator, sellId } = props
    
    const { chainId, web3 } = useSelector(
        (state) => state
    );
    const [nftInfo, setNftInfo] = useState(null)
    const router = useHistory()
    const toPage = (url) => {
        router.push(url)
    }
    useEffect(() => {
        async function fetchDetail() {
            let res = await axios.get(tokenURI);
            setNftInfo(res.data)
        }
        fetchDetail()
    }, [tokenURI, price])
    return (
        <>
            <div className="production-item">
                <div className="production-item-create">
                    <Link to={`/profile/index/${chainId}`}>{`${!!creator ? creator.slice(0, 6) + '...' + creator.slice(creator.length - 4, creator.length) : ' '}`}</Link>
                </div>
                <div className="production-works" onClick={() => { toPage(`/token/${chainId}/${collectionAddress}/${index}/${!!sellId ? sellId : 'null'}`) }}>
                    <img src={nftInfo?.image} alt="" className="production-works-img" />
                </div>
                <div className='production-works-name'>{nftInfo?.name}</div>
                <div className="production-item-price">
                    {price ? (<div>
                        <span style={{ fontWeight: 'bold', fontSize: '16px' }}>{getFloat(web3.utils.fromWei(price, 'ether'), 4)}</span>{' '}
                        <span>{getSymbol(chainId)[tokenPayment]}</span>
                    </div>) : <span> </span>}
                    <div className="production-standard">
                        <Tooltip title={isERC1155 ? 'ERC1155' : 'ERC721'} color='#6080b4'>
                            <Button className="production-button">
                                <img src={isERC1155 ? erc1155 : erc721} alt="" width="22px" />
                            </Button>
                        </Tooltip>
                    </div>
                </div>
            </div>
        </>
    )
}