import './detail.scss'
import collectionDefault from 'Assets/collection-default.png'
import { Link, useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { useCallback, useEffect, useState } from 'react'
import { getNftsByCollectionAddress } from 'Store/actions';
import ProductionItem from 'Components/ProductionItem'
import IconLoading from 'Components/IconLoading'
import { getDB } from 'Utils/orbit'
import { getContractAddress } from 'Utils/getContractAddress';
import NoData from 'Components/NoData';
import Logo from 'Assets/logo.png';
export default function CollectionDetail() {
    const { collectionAddress } = useParams()
    const { nftsOfCollectionAddress, walletAddress, orbitdb, chainId, convertErc721Tokens, nftList } = useSelector((state) => state)
    const [isOther, setIsOther] = useState(null); //判断是否为当前登录用户
    const [colInfo, setColInfo] = useState(null); //作品集信息
    const [loadingNFTs, setLoadingNFTs] = useState();
    const [nftsOnSale, setNftsOnSale] = useState([]);
    const [status, setStatus] = useState(true);
    const [nftsAll, setNftsAll] = useState([]);
    const dispatch = useDispatch()
    // 获取该作品集下的nft信息
    const fetchOwner = useCallback(async () => {
        dispatch(getNftsByCollectionAddress(collectionAddress));
    }, [collectionAddress, dispatch]);

    const fetchDB = async (dbAddress, orbitdb) => {
        // 连接db
        const db = await getDB(dbAddress, orbitdb)
        if (db) {
            // 获取当前作品集的信息
            let colInfo = await db.get(`${collectionAddress}_${chainId}`)
            setColInfo(colInfo)
        }
    }
    useEffect(() => {
        // 判断是否是平台作品集
        if (collectionAddress === getContractAddress(chainId)?.DidiERC721NFT) {
            const colInfo = {
                logo: Logo,
                name: 'DiDi'
            }
            setColInfo(colInfo)
        } else {
            // 根据环境获取orbitdb库的地址
            const dbAddress = process.env.REACT_APP_ORBIT_DB;
            if (!!orbitdb) {
                fetchDB(dbAddress, orbitdb)
            }
        }
    }, [orbitdb,collectionAddress])// eslint-disable-line react-hooks/exhaustive-deps

    useEffect(() => {
        // 判断作品集的创建者是不是当前用户
        if (colInfo?.creator?.toLowerCase() === walletAddress) {
            setIsOther(false)
        } else {
            setIsOther(true)
        }
    }, [colInfo, walletAddress])
    const filterCollectionInOnSale = useCallback(async () => {
        if (!!nftList) {
            let onSaleOfAddressToken = [];
            for (let i = 0; i < convertErc721Tokens.length; i++) {
                const collection = convertErc721Tokens[i];
                if (collection.addressToken.toLowerCase() === collectionAddress.toLowerCase()) {
                    onSaleOfAddressToken = collection.tokens;
                    break;
                }
            }
            setNftsOnSale(onSaleOfAddressToken);
        }
    }, [collectionAddress, convertErc721Tokens, nftList]);

    useEffect(() => {
        fetchOwner();
        filterCollectionInOnSale();
    }, [fetchOwner, filterCollectionInOnSale])

    useEffect(() => {
        if (nftsOnSale && nftsOfCollectionAddress && status) {
            setLoadingNFTs(true);
            const arrs = nftsOfCollectionAddress.map(item => {
                const data = nftsOnSale.find(i => item.index === i.index)
                return data ? { ...data } : { ...item }
            })
            setNftsAll(arrs);
            if (nftsAll.length >= 1) {
                return () => {
                    setLoadingNFTs(false);
                    setStatus(false)
                }
            }
        }
    }, [setNftsAll, nftsOnSale, nftsOfCollectionAddress, status, nftsAll.length])

    return (
        <>
            <div className="collection-detail">
                <div className='detail-info'>
                    <div className="collection-detail-header">
                        <div className="collection-detail-logo">
                            <img className="collection-logo" src={colInfo?.logo || collectionDefault} alt="" />
                        </div>
                        <div className="collection-detail-info">
                            <div className="collection-detail-name">{colInfo?.name}</div>
                            <div className="collection-detail-desc">

                                {colInfo?.description}
                            </div>

                            {/* <div class="text">
                        <label class="btn">展开</label>
                        <span>
                        
                            </span>
                        </div> */}
                        </div>
                    </div>
                    {!isOther ? (
                        <div className="collection-edit-btn">
                            <Link to={`/collection/edit/${collectionAddress}`}>编辑作品集</Link>
                        </div>
                    ) : null}
                    {loadingNFTs || loadingNFTs === null ? (
                        <div className='center' style={{ width: '100%', height: '100%' }}>
                            <IconLoading />
                        </div>
                    ) : (
                        <div className="pro-items" style={{ justifyContent: nftsAll?.length ? 'flex-start' : 'center' }}>
                            {
                                nftsAll?.length ? nftsAll.map((item, i) => (
                                    <ProductionItem
                                        isERC1155={item.isERC1155}
                                        index={item.index}
                                        tokenURI={item.tokenURI}
                                        price={item.price}
                                        tokenPayment={item.tokenPayment}
                                        sellId={item.sellId}
                                        collectionAddress={collectionAddress}
                                        creator={colInfo?.creator}
                                        key={i}>
                                    </ProductionItem>
                                )) : (
                                    <NoData />
                                )
                            }
                        </div>
                    )}

                </div>
            </div>
        </>
    )
}